.post {
    position: relative;
    width: 100%;

    margin-bottom: 30px;

    .post__constant {
        display: flex;
        max-width: 100%;
        padding: 10px;

        background-color: rgb(245, 249, 255);

        border: 2px solid rgb(209, 224, 255);
        border-radius: 5px;

        line-height: 1.2;

        transition-duration: 0.4s;

        &:hover {
            background-color: rgb(221, 234, 255);
            // border: 1px solid rgb(178, 205, 255);
            transition-duration: 0.4s;
        }
    }

    .post__image {
        width: 50px;
        height: 50px;
        margin-right: 20px;

        border-radius: 50%;
    }

    .post__text {
        display: flex;
        align-self: center;

        margin-right: 75px;
        padding-top: 3px;
        padding-bottom: 3px;

        font-size: 18px;
        word-break: break-all;

        overflow: hidden;
    }
}

.controls {
    position: absolute;
    right: 10px;
    top: 15px;

    .controls__button {
        opacity: 0.5;
        transition-duration: 0.2s;
    }

    .controls__buttonClose {
        margin-left: 8px;
        margin-top: 10px;
        color: red;  
    }

    .controls__button:hover {
        opacity: 1;
        transition-duration: 0.2s;

        cursor: pointer;
    }
}


//форма редактирования поста
.post__update {
    display: flex;
    max-width: 100%;
    padding: 10px;

    border: 2px solid;
    border-radius: 5px;

    line-height: 1.2;

    background-color: rgb(255, 255, 255);
    transition-duration: 0.4s;

    animation: border 3s linear infinite;

    @keyframes border {
        0% {
            border-color: rgb(255, 94, 94);
        }

        50% {
            border-color: rgb(255, 255, 255);
        }

        100% {
            border-color: rgb(255, 94, 94);
        }
    }

    .controls {
        position: absolute;

        top: 15px;

        cursor: pointer;

        div {
            opacity: 1;

            &:hover {
                color: red;
            }
        }
    }

}

.post__textareaContainer {
    position: relative;

    width: 100%;
    height: 100%;

    margin-right: 75px;

    padding-top: 3px;
    padding-bottom: 3px;

    display: flex;
    align-self: center;

    color: red;
    font-size: 18px;

    word-break: break-all;
    overflow: hidden;
}

.post__textarea {
    position: absolute;
    left: -5px;
    right: -5px;
    top: -2px;
    bottom: -3px;

    width: calc(100% + 10px);

    margin-right: 70px;
    padding: 5px;

    line-height: 1.2;
    font-size: 18px;
    word-break: break-all;

    background-color: rgb(255, 255, 255);
}

//форма предупреждения об удалении поста
.warning__container {
    position: absolute;
    top: -5px;
    right: 0;

    width: 150px;
    height: 70px;

    background-color: white;

    border: 2px solid rgb(255, 94, 94);
    border-radius: 5px;
}

.warning__title {
    margin-top: 10px;
    text-align: center;
}

.warning__buttons {
    display: flex;
    justify-content: center;

    margin-top: 5px;

    font-size: 16px;

    transition-duration: 0.4s;

    div {
        padding: 5px;
        padding-left: 10px;
        padding-right: 10px;
        cursor: pointer;
    }

    div:first-child{
        margin-right: 10px;

        transition-duration: 0.4s;

        &:hover {
            color: red;
            transition-duration: 0.4s;
        }
    }

    div:last-child{
        transition-duration: 0.4s;

        &:hover {
            color: cornflowerblue;
            transition-duration: 0.4s;
        }
    }
}