.profileStatus {
    font-size: 16px;

    overflow: hidden;
}

.profileStatus__Title {
    
}

.profileStatus__Description {
    margin-top: 10px;
    margin-bottom: 15px;
    height: 16px; 

    padding-top: 5px;
    padding-bottom: 20px;
    border-bottom: 1px dashed rgb(188, 206, 238);

    transition-duration: 0.5s;

    &:hover {
        border-bottom: 1px dashed rgb(218, 243, 219);
        background: rgb(233, 240, 253);
        transition-duration: 0.5s;
    }
}

.profileStatus__input {
    margin-top: 11px;
    margin-bottom: 16px;

    width: 226px;
    line-height: 1;

    background-color: rgb(233, 240, 253);

    overflow: hidden;
}