.profileInfo__Text {
    display: flex;
}

.profileInfo__TextLeft {
    width: 220px;
}

.profileInfo__Name {
    font-size: 20px;
    margin-bottom: 15px;
    font-weight: 500;
}

.profileInfo__Parametr {
    margin-bottom: 16px;

    
    font-size: 16px;

    p {
        margin-bottom: 10px;
    }
}

.bold {
    font-weight: 500;
}


//Контакти
.profileInfo__Contacts {
    margin-left: 30px;

    font-size: 16px;
}

.profileInfo__ContactsTitle {
    margin-bottom: 30px;
    
    font-size: 20px;
    font-weight: 500;
}

.profileInfo__Contact {
    display: flex;
    margin-top: 12px;

    font-size: 16px;
}

.profileInfo__ContactTitle {
    width: 70px;
}

.profileInfo__ContactValue {
    a {
        text-decoration: none;
        color: cornflowerblue;
        cursor: pointer;

        &:hover {
            color: rgb(19, 93, 230);
        }
    }
}



//Кнопка редактирования профиля
.profileInfo__button {
    margin: 0px auto 0px;
    width: 100%;
    max-width: 290px;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: cornflowerblue;
    color: #fff;
    border-radius: 3px;

    cursor: pointer;
    transition-duration: 0.5s;

    &:hover {
        background: rgb(63, 151, 63);
        transition-duration: 0.5s;
    }

    &:active {
        background: rgb(52, 124, 52);
        transition-duration: 0.5s;
    }
}